import "./styles.css";
import React, { useState, useRef } from "react";
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
} from "recharts";
import dayjs from "dayjs";
import {
  MantineProvider,
  Container,
  Title,
  Group,
  Button,
  Text,
  Space,
  Paper,
  Divider,
  Stack, FileInput,
} from "@mantine/core";
import { DatePicker } from "@mantine/dates";
import { useClickOutside } from "@mantine/hooks";
import "@mantine/core/styles.css";
import "@mantine/dates/styles.css";

const parseCSV = (csv: string) => {
  const lines = csv.trim().split("\n");
  const headers = lines[0].split(";");
  const data = lines.slice(1).map((line) => {
    const values = line.split(";");
    const entry: Record<string, string | number | Date> = {};
    headers.forEach((header, index) => {
      if (header === "datetime") {
        entry[header] = new Date(values[index]);
      } else {
        entry[header] = parseFloat(values[index]);
      }
    });
    return entry;
  });
  return data;
};

function datetimeFormatter(tick: string): string {
  return dayjs(tick).format("DD.MM.YYYY HH:mm");
}

export default function App() {
  const [data, setData] = useState<any[]>([]);
  const [dateRange, setDateRange] = useState<[Date | null, Date | null]>([
    null,
    null,
  ]);
  const [isPickerOpen, setIsPickerOpen] = useState(false); // Управляем отображением всплывающего окна

  const pickerRef = useClickOutside(() => setIsPickerOpen(false)); // Закрытие всплывающего окна при клике вне блока

  // Фильтрация данных по выбранному диапазону
  const filteredData =
    dateRange[0] && dateRange[1]
      ? data.filter((entry) => {
          const datetime = dayjs(entry.datetime);
          return (
            datetime.isAfter(dayjs(dateRange[0]).startOf("day")) &&
            (datetime.isBefore(dayjs(dateRange[1]).endOf("day")) ||
              datetime.isSame(dayjs(dateRange[1]).endOf("day")))
          );
        })
      : data;

  // Обработчик загрузки файла
  const handleFileUpload = (file: File | null) => {
    if (!file) return;

    const reader = new FileReader();
    reader.onload = (e) => {
      const text = e.target?.result as string;
      const parsedData = parseCSV(text);
      setData(parsedData);
    };
    reader.readAsText(file);
  };

  // Установка популярных диапазонов дат
  const setPresetRange = (range: "today" | "yesterday" | "last_7_days" | "last_30_days") => {
    const now = dayjs();
    let start: dayjs.Dayjs;
    let end: dayjs.Dayjs;

    switch (range) {
      case "today":
        start = now.startOf("day");
        end = now.endOf("day");
        break;
      case "yesterday":
        start = now.subtract(1, "day").startOf("day");
        end = now.subtract(1, "day").endOf("day");
        break;
      case "last_7_days":
        start = now.subtract(7, "day").startOf("day");
        end = now.endOf("day");
        break;
      case "last_30_days":
      default:
        start = now.subtract(30, "day").startOf("day");
        end = now.endOf("day");
        break;
    }

    setDateRange([start.toDate(), end.toDate()]);
    setIsPickerOpen(false); // Закрываем всплывающее окно
  };

  return (
    <MantineProvider>
      <Container my="lg">
        {/* Заголовок */}
        <Group justify="space-between" align="center">
          <Title order={1}>Home Climate Viewer</Title>

          {/* Триггер для выбора дат */}
          <div style={{ position: "relative" }}>
            {isPickerOpen ? (
              <div
                ref={pickerRef} // Привязываем обработку кликов вне блока
                style={{
                  position: "absolute",
                  right: 0,
                  zIndex: 1000,
                  backgroundColor: "white",
                  padding: "16px",
                  boxShadow: "0px 4px 10px rgba(0,0,0,0.1)",
                  borderRadius: "8px",
                  display: "flex",
                  gap: "16px",
                }}
              >
                {/* Левая часть: Кастомный выбор диапазона */}
                <div style={{ flex: 1 }}>
                  <DatePicker
                    type="range"
                    value={dateRange}
                    onChange={setDateRange}
                  />
                </div>

                <Divider orientation="vertical" />

                {/* Правая часть: Кнопки пресетов и закрытие */}
                <div style={{ flexBasis: "200px" }}>
                  <Stack gap="xs">
                    <Button
                      variant="outline"
                      onClick={() => setPresetRange("today")}
                    >
                      Today
                    </Button>
                    <Button
                      variant="outline"
                      onClick={() => setPresetRange("yesterday")}
                    >
                      Yesterday
                    </Button>
                    <Button
                      variant="outline"
                      onClick={() => setPresetRange("last_7_days")}
                    >
                      Last 7 Days
                    </Button>
                    <Button
                      variant="outline"
                      onClick={() => setPresetRange("last_30_days")}
                    >
                      Last 30 Days
                    </Button>
                    <Button
                      variant="outline"
                      color="red"
                      onClick={() => {
                        setDateRange([null, null]);
                        setIsPickerOpen(false);
                      }}
                    >
                      Reset
                    </Button>

                    {/* Кнопка закрытия */}
                    <Button
                      variant="default"
                      color="gray"
                      onClick={() => setIsPickerOpen(false)}
                    >
                      Close
                    </Button>
                  </Stack>
                </div>
              </div>
            ) : (
              <Text
                variant="link"
                style={{ cursor: "pointer", color: "#1d72b8" }}
                onClick={() => setIsPickerOpen(true)}
              >
                {dateRange[0] && dateRange[1]
                  ? `${dayjs(dateRange[0]).format("DD.MM.YYYY")} - ${dayjs(
                      dateRange[1]
                    ).format("DD.MM.YYYY")}`
                  : "Select Date Range"}
              </Text>
            )}
          </div>
        </Group>

        <Space h="md" />
        <FileInput
            label="Upload CSV file"
            placeholder="Click to upload"
            accept=".csv"
            onChange={(file) => handleFileUpload(file)}
        />

        <Space h="md" />
        {filteredData.length > 0 ? (
          <Paper shadow="xs" p="md" radius="md">
            <ResponsiveContainer width="100%" height={300}>
              <LineChart data={filteredData} margin={{ top: 20 }}>
                <CartesianGrid strokeDasharray="3 3" />
                <XAxis dataKey="datetime" tickFormatter={datetimeFormatter} />
                <YAxis />
                <Tooltip
                  labelFormatter={(value) => `Time: ${datetimeFormatter(value)}`}
                />
                <Legend />
                <Line
                  type="monotone"
                  dataKey="temperature"
                  stroke="#8884d8"
                  activeDot={{ r: 8 }}
                  name="Temperature (°C)"
                />
                <Line
                  type="monotone"
                  dataKey="humidity"
                  stroke="#82ca9d"
                  name="Humidity (%)"
                />
              </LineChart>
            </ResponsiveContainer>
          </Paper>
        ) : (
          <Text>Upload a CSV file and/or select a date range to visualize data.</Text>
        )}
      </Container>
    </MantineProvider>
  );
};
